import React from 'react';
import imgCart from './—Åart.png';
import ContentCart from './ContentCart.js';

class Cart extends React.Component {
  constructor() {
    super()
    this.state = {
      statusCart: false,
      classNameContent: "sidebar-cart__content_close"
    }
    this.openCart = this.openCart.bind(this)
  }

  openCart = () => {
    this.state.statusCart === false ?
          this.setState({classNameContent: "sidebar-cart__content_open"}) :
          this.setState({classNameContent: "sidebar-cart__content_close"})
    this.setState( prevState => {
      return {statusCart: !prevState.statusCart}
    })
  }

  connectWeb3 = async () => {
    await window.ethereum.request({ method: 'eth_requestAccounts' });
  }

  render(props) {
    let sum = 0
    for (let books of this.props.cartBook.cartBook) {
      sum = sum + books.price
    }
    const content = this.props.cartBook.cartBook.map(value =>
      <ContentCart key={value.id} contentCart={value} />)
    return (
      <>
        <div className="sidebar-cart">
          <div className="sidebar-cart__btn" onClick={this.openCart}>
            <img src={imgCart} alt='–∫–æ—Ä–∑–∏–Ω–∞' className="sidebar-cart__img" />
            <div className="sidebar-cart__value">
              <div className="sidebar-cart-text">{`–∫–Ω–∏–≥ - ${this.props.cartBook.cartBook.length} —à—Ç`}</div>
              <div className="sidebar-cart-text">{`—Å—É–º–º–∞ - ${sum} —Ä—É–±`}</div>
            </div>
          </div> 
          <button onClick={this.connectWeb3} className="connect__web3">–ü–æ–¥–∫–ª—é—á–∏—Ç—å—Å—è –∫ web3</button>
          <div className={this.state.classNameContent}>
            <div>{content}</div>
            <button onClick={() => { this.props.cartBook.getCart(sum); this.setState({classNameContent: "sidebar-cart__content_close"})}} className="sidebar-cart__content_btn">–û–ø–ª–∞—Ç–∏—Ç—å</button>
            <button onClick={() => { this.props.cartBook.removeCart(); this.setState({classNameContent: "sidebar-cart__content_close"})}} className="sidebar-cart__content_btn">–û—Ç–º–µ–Ω–∞</button>
          </div>
        </div>
      </>
    )
  }
}
export default Cart;